:root {
    --primary-dark: #333;
    --secondary-dark: #444;
    --primary-light: #eee;
    --secondary-light: #ddd;
}

* {
    box-sizing: border-box;
    font-family: 'robotoregular', sans-serif;
}

html, body {
    margin: 0;
    padding: 0;
    height: 100%;
}

body {
    display: grid;
    grid-template-rows: auto 1fr;
    grid-template-columns: auto 1fr;
    background-color: var(--primary-light);
}

/* ---------- Header ----------*/

header {
    grid-row: 1/ 2;
    grid-column: 1/3;
    background-color: var(--primary-dark);
    color: var(--primary-light);
    padding: 1rem;
}

header .wrapper {
    display: flex;
    justify-content: flex-start;
    align-items: center;
    margin-left: 1rem;
    width: 15rem;
    gap: 1rem;
}

header .title {
    font-size: 2rem;
}

.material-symbols-outlined.checkmark {
    font-size: 3rem; /* bigger icon */
}

/* ---------- Nav bar ----------*/

aside {
    grid-column: 1/2;
    background-color: var(--secondary-light);
    padding: 1.5rem;
}

aside h2 {
    margin-top: 4px;
    margin-bottom: 4pxrem;
    padding: .5rem;
}

aside ul {
    list-style-type: none;
    padding-left: 0;
}

#sidebar li {
    margin-bottom: 1.5rem;
    display: flex;
    justify-content: space-between;
    align-items: center;
    border-radius: 1rem;
    background-color: var(--secondary-light);
    transition: filter 0.2s ease;
    width: 12rem;
    overflow: hidden;
    padding: .5rem;
}

.project-close {
    display: grid;
    place-items: center;
}

#sidebar .style-help {
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 3px;
}

.project-swap,  .project-close, #add-project {
    border: none;
    background-color: var(--secondary-light);
}

.project-swap:hover, .project-close:hover, #add-project-hover {
    cursor: pointer;
}

.project-close {
    opacity: 0;
    transition: opacity 0.2s ease;
}

#sidebar li:hover .project-close { /* target when the li element is hovered */
    opacity: 1;
}


#sidebar li:hover, #add-project:hover {
    filter: brightness(0.85); /* 97% brightness = subtle darkening */
}

#add-project {
    padding: .5rem;
    border-radius: 1rem;
    width: 13rem;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    gap: 10px;
    position: relative;
}

aside form {
    display: flex;
    flex-direction: column;
    gap: 1rem;
    padding: .5rem;
}

.form-buttons {
    display: flex;
    justify-content: space-between;
}

.sub-form, .clear-form {
    width: 4rem;
}

.sub-form:hover, .clear-form:hover {
    cursor: pointer;
}


/* ---------- Main ----------*/

main {
    grid-column: 2/3;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    padding: 1rem;
    margin-left: 2rem;
    margin-right: 2rem;
}

main h2 {
    margin-left: 7px;
}

main ul {
    margin-left: 0;
    padding-left: 0;
}

main .circle {
    border: 2px solid black;
    width: 1.5rem;
    height: 1.5rem;
    border-radius: 1rem;
}

main li {
    list-style-type: none;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    gap: 1rem;
    margin-bottom: 1rem;
    transition: filter 0.2s ease;
    overflow: hidden;
    background-color: var(--primary-light);
    border-radius: 1rem;
    padding: .5rem;
}

main li:hover {
    filter: brightness(0.85);
}

main .circle:hover {
    cursor: pointer;
}

main .task-item, #add-task {
    display: flex;
    justify-content: flex-start;
    align-items: center;
    gap: 10px;
    width: 100%rem;
    border: none;
    border-radius: 1rem;
    transition: filter 0.2s ease;
}

main button:hover {
    filter: brightness(0.85);
}

#task-input {
    width: 100%;
    margin-top: 1rem;
}

.task-buttons {
    display: flex;
    justify-content: flex-start;
    gap: 1.5rem;
    margin-top: 1rem;
}

.task-buttons button {
    width: 5rem;
